<template>
  <div class="contents_title" v-if="this.setListAll">
    <h3>5 </h3>
  </div>
  <div class="contents-container" v-if="this.setListAll">
    <div v-for="(plant, id) in plantList" :key="id">
      <div class="contents_item_list" v-if="id < 5"> 
        <div v-on:click="$emit('plantClicked', plant)" style="border:0px solid red; margin-bottom: 0.4rem;">
          <div v-if="plant.plantImgUrl != ''" >
            <img class="image-card-mini" :src="require(`@/assets/images/${plant.file_url}`)">
          </div>
          <div class="image-card-mini" v-else style="text-align: center;">
            No Images.
          </div>
          <div class="plant_preview_text">
            {{ plant.plnt_nm }}
          </div>
        </div>
        <div class="plant_preview_keyword">
          <div class="plant_preview_keyword_list" v-for="(keywordInfo,id) in plant.kywrd_list" :key="id">
            <div v-on:click="this.$emit('sendKeyword', keywordInfo);">
              {{ keywordInfo.plnt_keyword_vw }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!--  title-->
  <div class="contents_title" v-if="this.setListAll">
    <h3> </h3>
  </div>
  <!--       . -->
  <div class="contents_title" v-if="this.setListAll==false && this.setListGb=='Keyword'">
    <h3>{{ clickedKeywordInfo.plnt_keyword_vw }} </h3>
  </div>
  <div class="contents_title" v-else-if="this.setListAll==false && this.setListGb=='Temperature'">
    <h3>  
      <text v-if="tmprRange[0] >- 100 && tmprRange[1] < 100">{{ tmprRange[0] }}C ~ {{ tmprRange[1] }}C</text>
      <text v-else-if="tmprRange[0] == -100"> {{tmprRange[1]}}C </text>
      <text v-else-if="tmprRange[1] == 100"> {{tmprRange[0]}}C </text>
    </h3>
  </div>
  <div v-if="this.setListAll==false && this.setListGb=='Keyword'" 
      style="display: flex; flex-direction: row; margin:auto;
              width: 90%; height: 1.5rem;">
    <div style="padding-left: 1rem; color: #61656A; font-size:0.8rem; font-weight: lighter;">
      {{clickedKeywordInfo.cd_expln}}
    </div>
  </div>
  <div class="contents-container">
    <div>
      <button class="button_basic" v-on:click="$emit('setAllPlantList', 'N');"></button>
      <button class="button_basic" v-on:click="$emit('setAllPlantList', 'Y');"></button>
    </div>
  </div>
  <!--    -->
  <div class="contents-container">
    <div class="contents_item_list" v-for="(plant,id) in plantList" :key="id">
      <div v-on:click="$emit('plantClicked', plant)" style="border:0px solid red; margin-bottom: 0.4rem;">
        <div v-if="plant.plantImgUrl != ''" >
          <img class="image-card-mini" :src="require(`@/assets/images/${plant.file_url}`)">
        </div>
        <div class="image-card-mini" v-else style="text-align: center;">
          No Images.
        </div>
        <div class="plant_preview_text">
          {{ plant.plnt_nm }}
        </div>
      </div>
      <div class="plant_preview_keyword">
        <div class="plant_preview_keyword_list" v-for="(keywordInfo,id) in plant.kywrd_list" :key="id">
          <!-- <div style="float:left; margin-right: 0.3rem;" v-on:click="this.$emit('sendKeyword', keywordInfo);"> -->
          <div v-on:click="this.$emit('sendKeyword', keywordInfo);">
            {{ keywordInfo.plnt_keyword_vw }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

